@charset "UTF-8";
:root {
  --font-family-base: "Noto Sans JP", "Noto Sans", sans-serif;
  --font-family-second: "Noto Serif JP", "Noto Serif", serif;
}

:root {
  --font-weight-regular: 400;
  --font-weight-normal: 500;
  --font-weight-bold: 700;
}

:root {
  --black: #000;
  --white: #fff;
  --bg-color: #fff;
}

:root {
  --z-index-header: 100;
  --z-index-to-top: 200;
  --z-index-drawer: 1000;
  --z-index-modal: 10000;
}

body {
  color: var(--black);
  font-family: var(--font-family-base);
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

/* グローバルリセット */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* テキストサイズの調整をリセット */
html {
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

/* ボディの基本設定 */
body {
  margin: 0;
  overflow-wrap: anywhere;
  word-break: normal;
  line-break: strict;
  min-height: 100vh;
  text-rendering: optimizeLegibility;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* デフォルトの余白とパディングを削除 */
ul,
ol,
button,
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd,
time,
small {
  margin: 0;
  padding: 0;
}

/* リストスタイルのリセット */
ul,
ol {
  list-style: none;
}

/* クラスなしのaタグにデフォルトのスタイルを適用 */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* インラインブロック要素 */
a,
span,
button {
  display: inline-block;
}

/* フォントとマージンのリセット */
small,
time,
figure {
  border: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline;
}

address {
  font-style: normal;
}

/* 画像の調整 */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* 記事内の自然な余白を設定 */
article > * + * {
  margin-top: 1em;
}

/* フォーム要素のフォント継承 */
input,
button,
textarea,
select {
  font: inherit;
}

/* フォームのリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  outline: none;
}

textarea {
  resize: vertical;
}

/* チェックボックスとラジオボタンを非表示 */
input[type=checkbox],
input[type=radio] {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  clip-path: polygon(0 0, 0 0, 0 0, 0 0);
  border: 0;
}

/* ボタンやラベルにカーソルを設定 */
input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

/* IE用の選択ボックスアイコンを非表示 */
select::-ms-expand {
  display: none;
}

/* aタグのスタイルとトランジション */
a {
  text-decoration: none;
  color: inherit;
  transition: 0.3s;
}

button {
  color: inherit;
}

/* ホバー時のカーソル設定 */
@media (any-hover: hover) {
  a:hover {
    cursor: pointer;
  }
}
/* PCでtelリンクを無効化 */
@media (min-width: 768px) {
  a[href*="tel:"] {
    pointer-events: none;
    cursor: default;
    text-decoration: none;
  }
}
/* タップ時のハイライトを透明に設定 */
a {
  -webkit-tap-highlight-color: transparent;
  /* リンクのハイライトを透明に */
}

button {
  -webkit-tap-highlight-color: transparent;
  /* ボタンのハイライトも透明に */
}

.inner {
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
  max-width: 500px;
}
@media screen and (min-width: 768px) {
  .inner {
    max-width: 1170px;
    padding-right: 35px;
    padding-left: 35px;
  }
}

/* ===============================================
# フェードアニメーション：IntersectionObserver＆GSAP用
=============================================== */
/* フェード */
.js-fade-in {
  opacity: 0;
  transition: opacity 2s ease-out;
}
.js-fade-in.is-active {
  opacity: 1;
}

/* 上方向にスライド */
.js-fade-up {
  opacity: 0;
  translate: 0 50px;
  transition: opacity 1.2s ease-out, translate 1.2s ease-out;
}
.js-fade-up.is-active {
  opacity: 1;
  translate: 0 0;
}

/* 左方向にスライド */
.js-slide-left {
  opacity: 0;
  translate: -50px 0;
  transition: opacity 1.2s ease-out, translate 1.2s ease-out;
}
.js-slide-left.is-active {
  opacity: 1;
  translate: 0 0;
}

/* 下方向にスライド */
.js-fade-down {
  opacity: 0;
  translate: 0 -50px;
  transition: opacity 1.2s ease-out, translate 1.2s ease-out;
}
.js-fade-down.is-active {
  opacity: 1;
  translate: 0 0;
}

/* 右方向にスライド */
.js-slide-right {
  opacity: 0;
  translate: 50px 0;
  transition: opacity 1.2s ease-out, translate 1.2s ease-out;
}
.js-slide-right.is-active {
  opacity: 1;
  translate: 0 0;
}

.js-color-curtain {
  position: relative;
  width: fit-content;
  clip-path: inset(50%);
}
.js-color-curtain::after {
  content: "";
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(90deg, #89B564 0%, #1A90BD 100%);
  transform: scaleX(0);
  transform-origin: 0 50%;
  pointer-events: none;
}
.js-color-curtain.is-active {
  animation: 1.2s forwards ease-in-out textView;
}
.js-color-curtain.is-active::after {
  animation: 1.2s forwards ease-in-out colorBox;
}

@keyframes textView {
  from {
    clip-path: inset(0 100% 0 0);
  }
  to {
    clip-path: inset(0 0 0 0);
  }
}
@keyframes colorBox {
  0%, 50% {
    transform-origin: 0 50%;
  }
  60%, 100% {
    transform-origin: 100% 50%;
  }
  60% {
    transform: scaleX(1);
  }
  100% {
    transform: scaleX(0);
  }
}
.js-scaleImg {
  overflow: hidden;
}
.js-scaleImg img {
  scale: 1.2;
  transition: scale 1s ease, opacity 1s ease;
  opacity: 0;
}
.js-scaleImg.is-active img {
  scale: 1;
  opacity: 1;
}

/* クリップパス */
.js-clip-img {
  overflow: hidden;
}
.js-clip-img img {
  scale: 1.2;
  transition: scale 1.3s ease, clip-path 1.3s ease;
  clip-path: inset(0 100% 0 0);
}
.js-clip-img.is-active img {
  clip-path: inset(0 0 0 0);
  scale: 1;
}

:root {
  --delay-1: 0.1s;
  --delay-2: 0.2s;
  --delay-3: 0.3s;
  --delay-4: 0.4s;
}

.--delay-1 {
  transition-delay: var(--delay-1);
}

.--delay-2 {
  transition-delay: var(--delay-2);
}

.--delay-3 {
  transition-delay: var(--delay-3);
}

.--delay-4 {
  transition-delay: var(--delay-4);
}

@media (max-width: 768px) {
  .--delay-1,
  .--delay-2,
  .--delay-3,
  .--delay-4 {
    transition-delay: 0.2s;
  }
}
@media (min-width: 1350px) {
  nav a {
    letter-spacing: 0.14em;
  }
}